@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.navbar{height:80px;display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center;background-color:#060b26}.menu-bars{margin-left:2rem;font-size:2rem;background:none}.nav-menu{width:250px;height:100vh;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;position:fixed;top:0;left:-100%;background-color:#060b26;transition:850ms}.nav-menu.active{left:0;transition:350ms}.nav-menu .nav-menu-items{width:100%}.nav-menu .nav-menu-items .navbar-toggle{width:100%;height:80px;display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center;background-color:#060b26}.nav-menu .nav-menu-items .nav-text{height:60px;padding:8px 0px 8px 16px;display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center;list-style:none}.nav-menu .nav-menu-items .nav-text a{height:100%;width:95%;padding:0 16px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;font-size:18px;color:#f5f5f5;border-radius:4px;text-decoration:none}.nav-menu .nav-menu-items .nav-text a:hover{background-color:#1a83ff}.nav-menu .nav-menu-items .nav-text a span{margin-left:16px}

*{box-sizing:border-box;margin:0;padding:0;font-family:'Lato', sans-serif}.home,.reports,.products,.team,.messages,.support{height:calc(100vh - 80px);display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;font-size:3rem}

